<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>日常管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/leave' }">外出审核记录</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="queryInfo.username" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getLeaveList">
            <el-button slot="append" icon="el-icon-search" @click="getLeaveList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addVisible=true">添加数据</el-button>
          <el-button type="danger" @click="batchDelete(tableChecked)">批量删除</el-button>
        </el-col>
      </el-row>
      <el-table
          :cell-style="rowClass"
          :data="LeaveList"
          :header-cell-style="headClass"
          :row-class-name="rowStyle"
          @selection-change="handleSelectionChange"
          border
          style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
            label="ID"
            prop="id"
            width="45px">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="username"
            width="80px">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="phone"
            width="110">
        </el-table-column>
        <el-table-column
            label="外出地点"
            prop="wcDd"
            width="100px">
        </el-table-column>
        <el-table-column
            label="外出原因"
            prop="wcYy">
        </el-table-column>
        <el-table-column
            label="外出时间"
            prop="wcTime">
        </el-table-column>
        <el-table-column
            label="申请状态"
            prop="stu"
            width="70px">
        </el-table-column>
        <el-table-column
            label="备注说明"
            prop="bz">
        </el-table-column>
        <el-table-column
            label="操作">
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary"
                         @click="editLeave(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="deleteLeave(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="queryInfo.pageNum"
          :page-size="queryInfo.pageSize"
          :page-sizes="[5,8,10,12,15]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>

    <!--添加dialog对话框-->
    <el-dialog
        :visible.sync="addVisible"
        title="添加记录"
        width="40%"
        @close="closeAddDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="LeaveUser" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="username">
          <el-input v-model="LeaveUser.username"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="LeaveUser.phone"></el-input>
        </el-form-item>
        <el-form-item label="外出地点" prop="wcDd">
          <el-input v-model="LeaveUser.wcDd"></el-input>
        </el-form-item>
        <el-form-item label="外出原因" prop="wcYy">
          <el-input v-model="LeaveUser.wcYy"></el-input>
        </el-form-item>
        <el-form-item label="外出时间" prop="wcTime">
          <el-date-picker
              style="float: left"
              v-model=LeaveUser.wcTime
              placeholder="外出时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="申请状况" prop="stu">
          <el-input v-model="LeaveUser.stu"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="bz">
          <el-input v-model="LeaveUser.bz"></el-input>
        </el-form-item>
      </el-form>
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="addLeave">确 定</el-button>
    </el-dialog>

    <!--编辑dialog对话框-->
    <el-dialog
        :visible.sync="editVisible"
        title="编辑记录"
        width="35%"
        @close="closeEditDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="LeaveUser" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="username">
          <el-input v-model="LeaveUser.username"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="LeaveUser.phone"></el-input>
        </el-form-item>
        <el-form-item label="外出地点" prop="wcDd">
          <el-input v-model="LeaveUser.wcDd"></el-input>
        </el-form-item>
        <el-form-item label="外出原因" prop="wcYy">
          <el-input v-model="LeaveUser.wcYy"></el-input>
        </el-form-item>
        <el-form-item label="外出时间" prop="wcTime">
          <el-date-picker
              style="float: left"
              v-model=LeaveUser.wcTime
              placeholder="外出时间"
              prop="wcTime"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="申请状况" prop="stu">
          <el-input v-model="LeaveUser.stu"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="bz">
          <el-input v-model="LeaveUser.bz"></el-input>
        </el-form-item>
      </el-form>
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="updateLeave">确 定</el-button>
    </el-dialog>


  </div>
</template>
<style>
.el-table .warnin-row {
  background: oldlace;
}
</style>

<script>

export default {
  name: "Leave",
  data() {
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      //  请求用户列表的参数
      queryInfo: {
        username: '',
        pageNum: 1,
        pageSize: 5,
      },
      total: 0,
      tableChecked: [],//被选中的记录数据-----对应“批量删除”传的参数值
      ids: [],//批量删除id
      LeaveUser: {
        id: null,
        username: '',
        wcTime: '',
        wcYy: '',
        wcDd: '',
        phone: '',
        stu: '',
        bz: ''
      },
      addVisible: false,
      editVisible: false,
      LeaveList: [],
      mobileFormRules: {
        phone: [
          {validator: checkMobile, trigger: 'blur'}
        ],
      },
    }
  },
  methods: {
    handleSelectionChange(val) {
      this.tableChecked = val;
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    rowStyle({row}) {
      // console.log(row)
      if (row.stu === '未审核') {
        return 'warnin-row';
      }
    },
    batchDelete(rows) {
      rows.forEach((item, index) => {
        this.ids.push(item.id);
      });
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$axios.post('/admin/leave/batchDeleteEgressUser', this.ids).then(res => {
          if (res.data.status !== 10000) {
            this.$message.error("删除失败！");
          }
          this.$message({type: 'success', message: '删除成功!'});
          this.getLeaveList();
          console.log('删除了', this.ids);
          this.ids = [];
        }).catch((e) => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      })
    },
    getLeaveList() {
      this.$axios.get('/admin/leave/egressList', {params: this.queryInfo}).then(res => {
        // console.log(res)
        if (res.data.data.total !== undefined) {
          if (res.data.status === 10000) {
            this.total = res.data.data.total;
            this.LeaveList = res.data.data.list;
            this.$message.success("查询列表成功！")
          } else {
            this.$message.error("查询列表失败！")
          }
        } else {
          this.LeaveList = [];
          if (res.data.status === 10000) {
            this.total = 1;
            this.LeaveList.push(res.data.data);
            this.$message.success("查询成功！")
          } else {
            this.$message.error("查询失败！")
          }

        }
      })
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getLeaveList();
    },
    handleCurrentChange(newNum) {
      this.queryInfo.pageNum = newNum;
      this.getLeaveList();
    },
    closeAddDialog() {

    },
    addLeave() {
      this.$refs["addFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/leave/addEgressUser', this.LeaveUser).then(res => {
            this.$message.success("新增信息成功！");
            this.addVisible = !this.addVisible;
            this.getLeaveList();
          })
        }
      })
    },
    editLeave(row) {
      this.LeaveUser = row
      // console.log(this.LeaveUser)
      this.editVisible = !this.editVisible;
    },
    updateLeave() {
      this.$refs["addFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/leave/updateUser', this.LeaveUser).then(res => {
            this.editVisible = !this.editVisible;
            this.getLeaveList();
          })
        }
      })
    },
    deleteLeave(id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '删除用户', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        //点击确定，发起删除用户
        this.$axios.get('/admin/leave/deleteEgressUser/' + id).then(res => {
          this.$message.success("删除成功！")
          this.getLeaveList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      })
    },
    closeEditDialog() {
      this.$refs.addFormRef.resetFields()
    }
  },
  created() {
    this.getLeaveList();
  }
}
</script>

